
.overflow_hidden {
	overflow: hidden;
}

.tjs-planet{
	position: absolute;
	@include pxToRem(top, 0);
	@include pxToRem(left, 0);
	@include pxToRem(bottom, 0);
	@include pxToRem(right, 0);
	//background: rgba(#f00, 0.3);
	@include user-select();
	opacity: 0;

	@include transition(opacity 0.3s ease);
}

.tooltip{
	position: absolute;
	display: none;
	@include pxToRem(margin-top, -33);
	@include pxToRem(margin-left, 20);
	z-index: 120;
	color: #fff;
	@include pxToRem(width, 250);

	&__inner{
		background: #0b0b0b;
		@include pxToRem(border-radius, 6);
		@include box-shadow(0 0 10px rgba(0,0,0,0.5));
		@include pxToRem(padding, 15, 10, 15, 20);

		&:before{
			content: '';
			position: absolute;
			display: block;
			border: solid transparent;
			@include pxToRem(border-width, 12);

			border-right-color: #0b0b0b;
			@include pxToRem(top, 12);
			@include pxToRem(left, -22);
		}
	}

	&__title{
		@include pxToRem(font-size, 17);
		@include pxToRem(margin-bottom, 10);
	}

	&__text{
		@include pxToRem(font-size, 12);
		@include pxToRem(max-height, 140);

		&-inner{

			@include pxToRem(padding-right, 15);
		}
	}

	&.is-open{
		display: block;
	}

}




.popup{
	display: none;
	color: #343434;

	.mCSB_container{

	}

	.mCSB_vertical{
		.mCSB{
			&_scrollTools{

				@include pxToRem(right, 25);

				.mCSB_dragger_bar{
					@include pxToRem(width, 10);
					background: rgba($colorText, 0.20);
				}
			}
			&_draggerContainer{
				@include pxToRem(top, 20);
				@include pxToRem(bottom, 20);
			}

		}
	}

	.mCSB_horizontal{
		.mCSB{
			&_scrollTools{

				@include pxToRem(bottom, 25);


				.mCSB_dragger_bar{
					@include pxToRem(height, 10);
					background: rgba($colorText, 0.20);
				}
			}
			&_draggerContainer{
				@include pxToRem(left, 20);
				@include pxToRem(right, 20);
			}

		}
	}

	&__inner{
		height: 100%;
		left: 0;
		overflow: auto;
		position: fixed;
		text-align: center;
		top: 0;
		white-space: nowrap;
		width: 100%;
		z-index: 102;

		&:before {
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
		}

	}

	&__layout{
		box-sizing: border-box;
		display: inline-block;
		margin: 0 auto;
		padding: 0;
		position: relative;
		vertical-align: middle;
		white-space: normal;
		@include pxToRem(width, 1180);
		z-index: 102;
		text-align: left;
	}


	&__overlay{
		background-color: rgba(0, 0, 0, 0.7);
		display: block;
		height: 100%;
		left: 0;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 101;
	}


	&__close{
		@include pxToRem(width, 28);
		@include pxToRem(height, 28);
		position: absolute;
		cursor: pointer;
		@include pxToRem(top, 25);
		@include pxToRem(right, 25);
		background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
		background-size: contain;
		opacity: 0.3;

		&:hover{
			opacity: 1;
		}
	}

	&__title{
		background: #1b1b1f;
		@include pxToRem(height, 78);
		@include pxToRem(line-height, 78);
		@include pxToRem(font-size, 22);
		font-weight: 500;
		text-align: center;
		@include pxToRem(padding, 0, 80);
		color: #fff;
		@include pxToRem(border-radius, 20, 20, 0, 0);
	}

	&__head{
		@include pxToRem(min-height, 50);
		background: #34373e;
		border-bottom: solid #434652;
		@include pxToRem(border-bottom-width, 1);
		@include box-sizing();
		position: relative;
		z-index: 1;

		@include box-shadow(0px 10px 20px 0px rgba(0, 0, 0, 0.35));

		&-tabl{
			display: table;
			width: 100%;
		}

		&-col{
			display: table-cell;
		}
	}

	&__content{
		@include pxToRem(min-height, 550);
		@include pxToRem(border-radius, 0, 0, 20, 20);
		background: #2f3138;
		//@include box-shadow(inset 0px 29px 20px -20px rgba(0, 0, 0, 0.35));

		@include clearfix();
		position: relative;
		color: $colorText;



		&-aside{
			@include pxToRem(width, 650);
			float: left;
			height: 100%;
			background: #34373e;
			@include box-sizing();

			//@include box-shadow(inset 0px 29px 20px -20px rgba(0, 0, 0, 0.35));
			@include pxToRem(border-radius, 0, 0, 0, 20);

			& ~ .popup__content-inner{
				@include pxToRem(margin-left, 650);
				height: 100%;
				@include box-sizing();
			}
		}


	}

	.counter{

		font-size: 0;
		overflow: hidden;

		&__value{
			@include pxToRem(height, 30);
			float: left;
			@include pxToRem(font-size, 17);
			@include pxToRem(line-height, 30);
			text-align: center;
			color: #fff;
			@include pxToRem(width, 70);
			background: #585c64;
			cursor: default;
			@include pxToRem(margin, 0, 2);
		}
		&__down{
			@include pxToRem(height, 30);
			float: left;
			@include pxToRem(width, 23);
			background: #40434c;
			position: relative;
			cursor: pointer;
			@include pxToRem(border-radius, 3, 0, 0, 3);

			&:before{
				content: '';
				@include pxToRem(width, 13);
				@include pxToRem(height, 3);
				position: absolute;
				top: 50%;
				left: 50%;
				@include pxToRem(margin-left, -6);
				@include pxToRem(margin-top, -1);
				background: #757782;
				@include pxToRem(border-radius, 10);
			}

			&:hover{
				&:before{
					background: #fff;
				}
			}
		}
		&__up{
			@include pxToRem(height, 30);
			@include pxToRem(width, 23);
			background: #40434c;
			float: left;
			position: relative;
			cursor: pointer;
			@include pxToRem(border-radius, 0, 3, 3, 0);

			&:before,
			&:after{
				content: '';
				@include pxToRem(width, 13);
				@include pxToRem(height, 3);
				position: absolute;
				top:50%;
				left: 50%;
				@include pxToRem(margin-left, -6);
				@include pxToRem(margin-top, -1);
				background: #757782;
				@include pxToRem(border-radius, 10);
			}

			&:after{
				@include transform(rotate(90deg));
			}

			&:hover{
				&:after,
				&:before{
					background: #fff;
				}
			}
		}
	}


	&__pp{
		position: absolute;
		top:0;
		left: 0;
		bottom: 0;
		right: 0;
		background: rgba(0,0,0,0.5);
		@include pxToRem(border-radius, 0, 0, 20, 20);
		z-index: 5;
		font-size: 0;
		text-align: center;
		display: none;

		&.is-open{
			display: block;
		}

		&:before{
			content: '';
			display: inline-block;
			vertical-align: middle;
			height: 100%;
		}


		&-layout{
			display: inline-block;
			position: relative;
			vertical-align: middle;
			@include pxToRem(width, 785);
			@include pxToRem(border-radius, 10);
			text-align: left;
			background: #34373e;
			@include pxToRem(min-height, 310);
			@include pxToRem(margin-bottom, 60);
			@include pxToRem(font-size, 15);

		}

		&-close{
			@include pxToRem(width, 28);
			@include pxToRem(height, 28);
			position: absolute;
			cursor: pointer;
			@include pxToRem(top, 20);
			@include pxToRem(right, 20);
			background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
			background-size: contain;
			opacity: 0.3;
			z-index: 1;

			&:hover{
				opacity: 1;
			}
		}

		&-preview{
			@include pxToRem(width, 345);
			position: absolute;
			top:0;
			left: 0;
			bottom: 0;
			background: #4d525f;
			@include pxToRem(border-radius, 10, 0, 0, 10);
		}

		&-control{
			position: absolute;
			@include pxToRem(bottom, 20);
			@include pxToRem(left, 20);
			@include pxToRem(right, 20);

			.btn{
				&:nth-of-type(2n){
					float: right;
				}
			}
		}

		&-content{
			@include pxToRem(margin-left, 345);
			@include pxToRem(padding, 20, 25);
			@include box-sizing();
			position: relative;
			@include pxToRem(min-height, 310);


			&__title{
				@include pxToRem(font-size, 21);
				color: #fff;
				@include pxToRem(margin-bottom, 20);
				@include pxToRem(margin-right, 40);
			}

			&__text{
				@include pxToRem(margin, 15, 0);
				color: $colorText;
			}



		}

		.btn{

			&_big{
				@include pxToRem(height, 48);
				@include pxToRem(padding, 0, 24);
				@include pxToRem(border-radius, 10);
				color: $colorText;
				@include pxToRem(font-size, 21);
				font-weight: 700;
				@include pxToRem(line-height, 48);
				border: solid transparent;
				@include pxToRem(border-width, 2);
				cursor: pointer;

				&:hover{
					color: #fff;
				}
			}

			&_dark{
				background: #2a2d34;
			}

			&_red{
				background: $colorRed;
				color: #fff;
			}

			&_border{
				border-color: $colorText;
			}
		}
	}
}



.popup_planet{

	.popup{

		&__head{
			@include pxToRem(padding, 28, 50);
			font-size: 0;

			&-title{
				@include pxToRem(font-size, 22);
				color: #fff;
				font-weight: 500;
				display: inline-block;
				vertical-align: middle;
				@include pxToRem(width, 315);
			}

			&-item{
				color: #9BA1B2;
				@include pxToRem(font-size, 17);
				display: inline-block;
				vertical-align: middle;
				@include pxToRem(width, 100);
				text-align: center;

				&_c{
					@include pxToRem(padding, 0, 55);
				}
			}
		}


		&__content{
			@include pxToRem(height, 550);
		}
	}

	.planet{


		&__list{
			@include pxToRem(padding, 20, 65, 100, 25);
		}

		&__item{

			img{
				display: block;
				width: 100%;
				height: 100%;
			}

			@include pxToRem(border-radius, 10);
			border: solid rgba($colorDark, 0.3);
			@include pxToRem(border-width, 1);
			@include pxToRem(padding, 20);
			font-size: 0;
			@include pxToRem(margin, 10, 0);
			position: relative;

			&:hover{
				background: rgba($colorText, 0.15);

				.planet__item-next{
					background: rgba($colorText, 0.15);
				}
			}

			&.is-open{
				.planet__item-all{
					display: block;
				}
				.planet__item-name-title{
					.arrow{
						@include transform(scale(-1));
					}
				}
			}

			&-next{
				@include pxToRem(width, 65);
				top: 0;
				right: 0;
				bottom: 0;
				position: absolute;
				@include pxToRem(border-radius, 0, 10, 10, 0);
				cursor: pointer;

				&:hover{
					background: rgba($colorText, 0.3)!important;

					&:before{
						opacity: 0.5;
					}
				}

				&:before{
					content: '';
					@include pxToRem(width, 38);
					@include pxToRem(height, 38);
					background: url("/img/popup-planet-arrow-next.svg") no-repeat 50% 50%;
					background-size: contain;
					position: absolute;
					top:50%;
					left: 50%;
					@include pxToRem(margin-left, -19);
					@include pxToRem(margin-top, -19);
					opacity: 0.3;
				}
			}

			&-name{
				@include pxToRem(width, 315);
				@include pxToRem(min-height, 10);
				display: inline-block;
				overflow: hidden;

				&-view{
					@include pxToRem(width, 76);
					@include pxToRem(height, 76);
					border: solid #2a2c33;
					@include pxToRem(border-width, 1);
					border-radius: 50%;
					@include box-shadow(0 0 0 1px #373a42);
					@include pxToRem(margin, 1);
					background: #23252a;
					float: left;

					img{
						max-height: 100%;
						max-width: 100%;
						display: block;
					}
				}

				&-title{
					@include pxToRem(margin-left, 92);
					@include pxToRem(font-size, 19);
					color: #fff;
					@include pxToRem(margin-bottom, 3);
					cursor: pointer;
					position: relative;
					@include pxToRem(padding-right, 35);

					.arrow{
						display: inline-block;
						position: absolute;
						@include pxToRem(margin-top, 5);
						//top: 0;
						//right: 0;
						@include pxToRem(width, 26);
						@include pxToRem(height, 17);
						background: url("/img/popup-planet-arrow-bottom.svg") no-repeat 50% 50%;
						background-size: contain;
						vertical-align: middle;
						@include pxToRem(margin-left, 10);
					}
				}

				&-info{
					@include pxToRem(margin-left, 92);
					@include pxToRem(font-size, 17);
					color: #8990AD;
					@include pxToRem(margin-bottom, 10);
				}

				&-icons{
					@include pxToRem(margin-left, 92);

					.ico{
						display: inline-block;
						vertical-align: top;
						@include pxToRem(margin, 0, 5, 5, 0);
						@include pxToRem(width, 18);
						@include pxToRem(height, 18);
						border-radius: 50%;
						background: #23252a;
					}
				}
			}

			&-keep{
				display: inline-block;
				text-align: center;
				@include pxToRem(width, 100);

				&_c{
					@include pxToRem(padding, 0, 55);
				}
			}

			&-row{

			}

			&-all{

				display: none;

				.planet__item-row{
					@include pxToRem(margin, 20, 0, 10);
				}
			}
		}
	}
}


.popup_building{
	.popup{
		&__head{

			@include pxToRem(padding, 15, 50);
			font-size: 0;
			text-align: center;

			&-item{
				@include pxToRem(padding, 0, 20);
				@include pxToRem(min-height, 48);
				@include pxToRem(line-height, 48);
				background: #2a2d34;

				@include pxToRem(font-size, 21);
				font-weight: 600;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				@include pxToRem(border-radius, 10);
				color: #fff;
				@include pxToRem(margin, 0, 12);

				.ico{
					@include pxToRem(width, 24);
					//@include pxToRem(height, 24);
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
					@include pxToRem(margin-right, 5);
				}

				span{
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
				}

				&:hover{
					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}

				&.is-active{
					background: $colorRed;

					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}
			}
		}


		&__content{
			@include pxToRem(height, 550);
			background: #2a2c32;

			&-image{
				@include pxToRem(height, 230);
			}

			.tabs{
				color: #5B5F6B;
				font-size: 0;

				&__item{
					display: inline-block;
					vertical-align: bottom;
					@include pxToRem(height, 40);
					@include pxToRem(line-height, 40);
					@include pxToRem(font-size, 17);
					@include pxToRem(padding, 0, 20, 0, 28);
					@include pxToRem(border-radius, 10, 15, 0, 0);
					position: relative;
					font-weight: 500;
					cursor: pointer;
					@include user-select();

					&:hover{
						text-shadow: 0 0 10px rgba(#fff, 0.5);
						color: #fff;
					}

					&.is-active{
						cursor: default;
						background: #34373e;
						color: #fff;
						text-shadow: 0 0 10px rgba(#fff, 0.5);

						&:after{
							content: '';
							display: block;
							position: absolute;
							border: solid transparent;
							@include pxToRem(border-width, 18);
							@include pxToRem(border-bottom-width, 32);
							border-bottom-color: #34373e;
							bottom: 0;
							@include pxToRem(right, -16);
						}
					}
				}

				&__content{
					background: #34373e;
					@include pxToRem(height, 280);
					@include pxToRem(border-radius, 0, 0, 20, 20);
					@include box-shadow(0px -29px 20px -20px rgba(0, 0, 0, 0.35));
					overflow: hidden;



					.build{
						&__list{
							@include pxToRem(padding, 0, 16, 60);
							overflow: hidden;
							margin-top: 0;
						}

						&__item{
							@include pxToRem(width, 116);
							@include pxToRem(height, 200);
							background: #3a3d45;
							border: solid #2a2c33;
							@include pxToRem(border-width, 2);
							@include box-shadow(inset 0 0 0 0.125rem #484c57);
							@include pxToRem(border-radius, 8);
							float: left;
							@include pxToRem(margin, 10, 6);
							text-align: center;
							@include pxToRem(padding-top, 15);

							.keep{
								&__item{
									z-index: 5;

									&-text{
										color: #8890ac;
									}

									&-info{
										@include pxToRem(top, -18);

										&-close{
											@include pxToRem(font-size, 15);
											@include pxToRem(padding, 2, 0, 6);

											.ico{
												@include pxToRem(width, 15);
												@include pxToRem(height, 15);
											}
										}

										&-text{
											@include pxToRem(margin-top, 110);
											border: 0 solid #292b31;
											@include pxToRem(border-bottom-width, 2);
											@include pxToRem(padding-bottom, 7);
											@include pxToRem(margin-bottom, 5);
											@include pxToRem(max-height, 48);
										}

										&-header{
											padding-top: 0;
											margin-bottom: 0;
											height: auto;
											@include pxToRem(padding-bottom, 7);
											p{
												@include pxToRem(margin-bottom, 5);
											}
										}
									}
								}
							}

							&:hover{
								background: #40434c;
								color: #fff;

								.keep__item-text{
									color: #fff;
								}
							}

							&:active,
							&.is-active{
								background: #2a2c32;
								color: #fff;
								border-color: #484c57;
								@include box-shadow(inset 0 0 0 0.125rem #2a2c33);

								.keep__item-text{
									color: #fff;
								}
							}

							&.is-locked{
								background: #34373e!important;
								color: #525561!important;
								border-color: #2a2c33!important;
								@include box-shadow(inset 0 0 0 0.125rem #484c57!important);

								.keep__item-text{
									color: #525561!important;
								}
							}
						}
					}


				}
			}
		}
	}

}

