							@include pxToRem(border-radius, 10);
							background: #3c3f47;
							@include box-shadow(inset 0 0 0 0.125rem #484c57);
							@include pxToRem(margin-bottom, 2);

							font-size: 0;

							&:after{
								content: '';
								display: inline-block;
								height: 100%;
								vertical-align: middle;
							}

							span{
								display: inline-block;
								vertical-align: middle;
								width: 100%;
								max-height: 100%;

								img{
									max-width: 100%;
									max-height: 100%;
								}
							}
						}

						&-name{
							@include pxToRem(font-size, 14);
							white-space: nowrap;
							text-overflow: ellipsis;
							overflow: hidden;
						}

						&-input{
							position: absolute;
							opacity: 0;

							&:checked{
								~ .fleet__target-item-img{
									@include box-shadow(inset 0 0 0 0.125rem #21232b);
									border-color: #434650;
									background: #21232b;
								}
							}

							&:disabled{
								~ .fleet__target-item-img{
									@include box-shadow(none);
									border-style: dotted;
									border-color: #444751;
									background: url("/img/ico/ico-lock.svg") no-repeat 50% 50%;
									@include pxToRem(background-size, 20, 20);
								}
							}
						}
					}


					&-info{
						overflow: hidden;
						@include pxToRem(padding, 1, 0);

						&-col{
							float: left;
							@include pxToRem(width, 275);

							&_time{
								@include pxToRem(width, 240);
								@include pxToRem(margin-left, 45);
							}

							&_stat{
								@include pxToRem(font-size, 21);
								text-align: right;
								@include pxToRem(width, 500);
								@include pxToRem(margin-left, 40);

								.btn{
									border-color: #4b4d5a;
									@include pxToRem(height, 36);
									@include pxToRem(line-height, 36);
									@include pxToRem(margin-left, 20);
									@include pxToRem(border-radius, 5);
									&:hover{
										color: #fff;
									}
								}
							}
						}
					}
				}

				&__store{

					@include pxToRem(padding, 0, 40, 20);

					&-table{
						display: table;
						width: 100%;

						@include pxToRem(border-spacing, 0, 22);
					}

					&-row{

						display: table-row;
					}

					&-item{

						display: table-cell;
						vertical-align: middle;
						text-align: right;
						white-space: nowrap;

					}

					&-name{

						display: inline-block;
						vertical-align: middle;

						.ico{
							display: inline-block;
							vertical-align: middle;
							border-radius: 50%;
							@include pxToRem(width, 19);
							@include pxToRem(height, 19);
							background: #41444f;
							@include pxToRem(margin-right, 5);

							img{
								display: block;
								width: 100%;
								height: 100%;
							}
						}

						span{
							display: inline-block;
							vertical-align: middle;
						}
					}

					&-counter{

						display: inline-block;
						vertical-align: middle;

						.counter{
							@include pxToRem(margin-left, 10);

							&__value{
								@include pxToRem(width, 165);
							}
						}
					}

					&-cargo{
						background: #34373e;
						border: solid #27282d;
						@include pxToRem(border-width, 1);
						@include pxToRem(border-radius, 8);

						@include pxToRem(padding, 25, 30, 25, 40);
						overflow: hidden;


						&-title{
							@include pxToRem(font-size, 19);
							color: #fff;
							@include pxToRem(width, 175);
							float: left;
							@include pxToRem(margin-top, 8);
						}
						&-fill{
							float: left;
							@include pxToRem(width, 661);
							position: relative;
							@include pxToRem(margin-top, 20);
						}
						&-current{
							position: absolute;
							left: 0;
							@include pxToRem(bottom, 22);
						}
						&-bulk{
							position: absolute;
							right: 0;
							@include pxToRem(bottom, 22);
						}
						&-line{
							@include pxToRem(height, 15);
							border: solid #1b1c20;
							@include pxToRem(border-width, 3);
							@include pxToRem(border-radius, 4);

							/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6fc841+0,fdc244+50,ff2511+100 */
							background: #6fc841; /* Old browsers */
							background: -moz-linear-gradient(left,  #6fc841 0%, #fdc244 50%, #ff2511 100%); /* FF3.6-15 */
							background: -webkit-linear-gradient(left,  #6fc841 0%,#fdc244 50%,#ff2511 100%); /* Chrome10-25,Safari5.1-6 */
							background: linear-gradient(to right,  #6fc841 0%,#fdc244 50%,#ff2511 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

							position: relative;
							overflow: hidden;
						}
						&-progress{
							position: absolute;
							@include pxToRem(top, 0);
							@include pxToRem(left, 0);
							@include pxToRem(bottom, 0);
							background: url("/img/progress-point.svg") repeat-x 0 0;
							background-size: auto 100%;

							&:after{
								content: '';
								display: block;
								position: absolute;
								top:0;
								bottom: 0;
								left: 100%;
								background: #1b1c20;
								@include pxToRem(width, 1000);
							}
						}
						&-send{

							float: right;

							.btn{
								border-color: #4f525e;
								@include pxToRem(height, 36);
								@include pxToRem(line-height, 36);
								@include pxToRem(margin-left, 20);
								@include pxToRem(border-radius, 5);

								&:hover{
									color: #fff;
								}
							}
						}
					}

				}
			}
		}
	}
}



.popup_buy{

	.popup{
		&__head{
			overflow: hidden;
			@include pxToRem(padding, 15, 35);
			font-size: 0;
			text-align: center;

			&-item{
				@include pxToRem(padding, 0, 20);
				@include pxToRem(min-height, 42);
				@include pxToRem(line-height, 42);
				background: #2a2d34;
				border: solid #2a2d34;
				@include pxToRem(border-width, 4);

				@include pxToRem(font-size, 21);
				font-weight: 600;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				@include pxToRem(border-radius, 10);
				color: #fff;
				@include pxToRem(margin, 0, 23);
				position: relative;

				span{
					display: inline-block;
					vertical-align: middle;
					opacity: 0.3;
				}

				&:hover{
					span{
						opacity: 1;
					}
				}

				&_back{
					float: left;
					margin: 0;

					span{
						color: $colorText;
						opacity: 1;
					}
				}

				&_next{
					float: right;
					margin: 0;
					background: #9ba0b1;
					border-color: #9ba0b1;
					color: #1b1b1f;

					span{
						opacity: 1;
					}
				}
			}

			&-title{
				text-align: center;
				@include pxToRem(font-size, 18);
				font-weight: 600;
				color: #fff;
				@include pxToRem(width, 500);
				margin: 0 auto;
				@include pxToRem(margin-top, 15);
			}
		}


		&__content{
			background: #34373e;

			.buy{

				&__list{
					@include pxToRem(min-height, 150);
					font-size: 0;
					text-align: center;
					background: #2f3138;
					overflow: hidden;

					.mCSB_horizontal .mCSB_scrollTools{
						@include pxToRem(bottom, 10);
					}
				}

				&__item{
					text-align: left;
					position: relative;
					display: inline-block;
					@include pxToRem(font-size, 15);
					@include pxToRem(width, 325);
					@include pxToRem(height, 115);
					@include pxToRem(border-radius, 10);
					border: #2a2c33 solid;
					@include pxToRem(border-width, 2);
					@include pxToRem(margin, 20, 7);
					@include pxToRem(padding, 10, 10);
					@include box-sizing();

					&-remove{
						@include pxToRem(width, 28);
						@include pxToRem(height, 28);
						position: absolute;
						cursor: pointer;
						@include pxToRem(top, 10);
						@include pxToRem(right, 10);
						background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
						background-size: contain;
						opacity: 0.3;

						&:hover{
							opacity: 1;
						}
					}

					&-logo{
						float: left;
						@include pxToRem(padding, 0);
					}

					&-info{
						@include pxToRem(margin-left, 110);
					}

					&-name{
						font-size: 19px;
						font-weight: 300;
						color: #fff;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						@include pxToRem(margin-bottom, 5);
						@include pxToRem(margin-top, 5);
						@include pxToRem(margin-right, 25);
					}

					&-val{
						font-size: 16px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						@include pxToRem(margin-bottom, 10);
					}

					&-price{
						font-size: 21px;
						font-weight: 500;
						color: #fff;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}

				}

				&__pay{
					text-align: center;
					&-result{
						@include pxToRem(font-size, 27);
						color: #fff;
						font-weight: 500;
						@include pxToRem(margin-top, 25);
						@include pxToRem(margin-bottom, 40);
					}
					&-title{
						@include pxToRem(font-size, 16);
						font-weight: 500;
						@include pxToRem(margin-bottom, 5);
					}
					&-list{}

					&-item{
						display: inline-block;
						@include pxToRem(width, 116);
						@include pxToRem(height, 196);
						@include pxToRem(padding, 10);

						@include pxToRem(border-radius, 10);
						background: #3a3d45;
						border: solid #2a2c33;
						@include pxToRem(border-width, 2);
						@include box-sizing();
						@include box-shadow(inset 0 0 0 1px #464a56);
						text-align: center;
						@include pxToRem(margin, 5, 4);
						cursor: default;
						font-size: 0;

						&:before{
							content: '';
							height: 100%;
							display: inline-block;
							vertical-align: middle;
						}


						&:hover{
							background: #34373e;
						}
					}
					&-inner{
						@include pxToRem(font-size, 13);
						display: inline-block;
						vertical-align: middle;
					}
					&-logo{
						.ico{
							@include pxToRem(width, 90);
							@include pxToRem(height, 90);
						}
					}
					&-name{}
				}

			}
		}
	}
}












.popup_stat{

	.popup{

		&__head{

			color: $colorText;
			@include pxToRem(padding, 25, 15);

			&-tabl{

			}

			&-col{
				@include pxToRem(padding, 0, 20);
				@include pxToRem(font-size, 14);
				vertical-align: top;
			}

			&-item{
				@include pxToRem(padding, 0, 20);
				@include pxToRem(min-height, 48);
				@include pxToRem(line-height, 48);
				background: #2a2d34;

				@include pxToRem(font-size, 21);
				font-weight: 600;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				@include pxToRem(border-radius, 10);
				color: #fff;
				@include pxToRem(margin, 0, 12);

				.ico{
					@include pxToRem(height, 20);
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
					@include pxToRem(margin-right, 5);
				}

				span{
					display: inline-block;
					vertical-align: middle;
					opacity: 0.5;
				}

				&:hover{
					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}

				&.is-active{
					background: $colorRed;

					.ico{
						opacity: 1;
					}

					span{
						opacity: 1;
					}
				}
			}

		}




	}


	.stat{

		&__head{

			overflow: hidden;
			@include pxToRem(padding, 25, 0);

			&-item{

				float: left;
				@include pxToRem(font-size, 17);
				@include pxToRem(line-height, 21);
				display: block;

				&_1{
					@include pxToRem(margin-left, 125);
					@include pxToRem(width, 300);
				}

				&_2{
					@include pxToRem(width, 165);
				}

				&_3{
					@include pxToRem(width, 245);
				}

				&.is-active{
					@include pxToRem(font-size, 21);
					font-weight: 600;
					color: #fff;
				}
			}
		}


		&__table{
			@include pxToRem(height, 400);
			@include box-shadow(inset 0px -29px 20px -20px rgba(0, 0, 0, 0.35));
		}

		&__list{
			@include pxToRem(padding, 5, 60, 20, 20);
		}

		&__item{

			border: solid #26272d;
			@include pxToRem(border-width, 1);
			@include pxToRem(border-radius, 5);
			display: table;
			width: 100%;
			@include pxToRem(margin-bottom, 10);

			&:hover{
				background: #34373e;
			}

			&-col{
				display: table-cell;
				vertical-align: middle;
				@include pxToRem(padding, 15);
				@include pxToRem(font-size, 19);
				color: #fff;

				.keep__item{
					@include pxToRem(width, 60);
					@include pxToRem(height, 60);
				}

				&_image{
					@include pxToRem(width, 60);
				}

				&_name{
					@include pxToRem(width, 250);
				}

				&_info{
					text-align: center;
					@include pxToRem(width, 200);

					.ico{

						display: inline-block;
						vertical-align: middle;
						@include pxToRem(height, 20);
						opacity: 0.3;
						@include pxToRem(margin-left, 5);

						&:hover{
							opacity: .5;
						}

						img{
							display: block;
							height: 100%;
						}
					}

					.label{
						span{
							@include pxToRem(font-size, 13);
							color: $colorText;
							display: inline-block;
							@include pxToRem(height, 21);
							@include pxToRem(line-height, 21);
							border: solid #41444f;
							@include pxToRem(border-width, 1);
							@include pxToRem(border-radius, 3);
							@include pxToRem(padding, 0, 12);
						}
					}
				}

				&_clear{

					@include pxToRem(width, 80);
					text-align: center;

					.ico{

						@include pxToRem(width, 28);
						@include pxToRem(height, 28);
						cursor: pointer;
						display: inline-block;
						background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
						background-size: contain;
						opacity: 0.3;

						&:hover{
							opacity: 1;
						}
					}
				}
			}
		}

		&__foot{
			@include pxToRem(padding, 30, 210, 0, 40);
			position: relative;

			&-text{
				text-align: right;

				.val{
					color: #fff;
				}
			}

			&-clear{

				@include pxToRem(width, 28);
				@include pxToRem(height, 28);
				position: absolute;
				cursor: pointer;
				@include pxToRem(top, 25);
				@include pxToRem(right, 100);
				background: url("/img/ico/ico-close.svg") no-repeat 50% 50%;
				background-size: contain;
				opacity: 0.3;

				&:hover{
					opacity: 1;
				}
			}
		}
	}
}